import React from "react";
import Logo from "./images/zonasys_icon.png";

function Title() {
  return (
    <div classNameName="container-fluid">
      <div
        id="carouselExampleControls"
        className="carousel slide"
        data-ride="carousel"
      >
        <div className="carousel-inner bg-success">
          <div className="carousel-item active" id="carousel-item1">
            <div className="container text-center text-light">
              <img src={Logo} alt="" />
              <h1 className="text-ubuntu" style={{textShadow:'2px 4px 6px #000'}}>Unveil its "Goal"</h1>A Vision For
              Business Growth
              <p>
                The company aims to increase more market share for domestic and
                overseas countries by expand domestic and overseas branches
                office, with the ultimate goal of becoming a leader in target
                businesses.
              </p>
              
              <a href="#services" className="btn btn-outline-light">Get Started</a>
              <hr />
            </div>
          </div>
          <div className="carousel-item" id="carousel-item2">
            <div className="row">
              <div className="col-lg-6 col-md"></div>
              <div className="col-lg-6 col-md text-ubuntu" id="about-slide">
                <div className="container">
                  <h1>About us</h1>
                  <p>
                    Developing software for Hospitality Industry Hotel Travel
                    agency All Entertainment and Restaurants Internet (Homepage,
                    E-Commerce) to be served world-widecommunication for all
                    businesses related Hardware and networking To provides
                    Turn-Keys for our target group
                  </p>
                  <button className="btn btn-lg btn-dark">More info</button>
                </div>
              </div>
            </div>
            {/* <img src="https://images.pexels.com/photos/1148820/pexels-photo-1148820.jpeg?cs=srgb&dl=pexels-panumas-nikhomkhai-1148820.jpg&fm=jpg" className="d-block w-100" alt="..." /> */}
          </div>
          <div className="carousel-item" id="carousel-item3">
            {/* <img src="..." className="d-block w-100 h-450" alt="..." /> */}
            <div className="container text-center align-center" id="business">
                
            <h3>BUSINESS OBJECTIVE</h3>
              <p>
                The investment privileges are fully supported by government
                which we have already operated.
              </p>
            </div>
          </div>
        </div>
        <a
          className="carousel-control-prev"
          href="#carouselExampleControls"
          role="button"
          data-slide="prev"
        >
          <span
            className="carousel-control-prev-icon"
            aria-hidden="true"
          ></span>
          <span className="sr-only">Previous</span>
        </a>
        <a
          className="carousel-control-next"
          href="#carouselExampleControls"
          role="button"
          data-slide="next"
        >
          <span
            className="carousel-control-next-icon"
            aria-hidden="true"
          ></span>
          <span className="sr-only">Next</span>
        </a>
      </div>
    </div>
  );
}

export default Title;
